export default{
    methods:{
        getAllNews(){
            return this.$store.dispatch('getAllNews');
        },
        getCompanyNews(company_id){
            return this.$store.dispatch('companyNews',company_id);
        },
        getNews(id){
            return this.$store.dispatch('getNews',id);
        },
    },
    computed:{
        allNews(){
            return this.$store.state.news.list
        },
        news(){
            return this.$store.state.news.current
        },

    }
}