<template>
    <div class="container">
        <element-wrapper>
            <div class="element-actions d-none d-sm-block">
                <router-link :to="{name:'news.create'}" class="btn btn-primary btn-sm">
                    <i class="os-icon os-icon-ui-22"></i><span>Nova notícia</span>
                </router-link>
            </div>
            <h6 class="element-header">
                Notícias
            </h6>
            <div class="table-responsive">
                <table class="table table-padded text-center">
                    <thead>
                    <tr>
                        <!--<th></th>-->

                        <th>
                            Título
                        </th>

                        <th>
                            Autor
                        </th>
                        <th>
                            Resumo
                        </th>
                        <th>
                            Ações
                        </th>
                    </tr>
                    </thead>
                    <tbody>
                    <tr v-for="news in allNews">
                        <td>
                            {{news.title}}
                        </td>
                        <td class="text-center">
                            {{news.author.data.name}}
                        </td>
                        <td>
                            {{news.resume}}
                        </td>
                        <td class="row-actions">

                            <router-link :to="{name:'news.show', params:{id:news.id}}" target="_blank">
                                <i class=" fas fa-eye text-info"></i>
                            </router-link>

                            <router-link :to="{name:'admin.news.edit',params:{id:news.id}}">
                                <i class="os-icon os-icon-pencil-1 text-warning"></i>
                            </router-link>

                            <a class="danger" href="#"><i class="os-icon os-icon-ui-15"></i></a>
                        </td>
                    </tr>
                    </tbody>
                </table>
            </div>
        </element-wrapper>
    </div>
</template>

<script>
    import NewsMixins from '../../../mixins/news'

    export default {
        mixins: [NewsMixins],
        created() {
            this.getCompanyNews(localStorage.getItem('company_id'));
        },
    }
</script>